import { useEffect } from "react";
import { useToggle } from "@/hooks/useToggle"; // ה-Custom Hook שנטפל בווריאנטים
import { useWeatherQuery } from "@/hooks/useWeatherQuery"; // קריאה למידע על מזג אוויר
import { Variant } from "@/types";

type WeatherDisplayProps = {
  city: string;
  variant: Variant;
};

export function WeatherDisplay({ city, variant }: WeatherDisplayProps) {
  const { value: isVariantA, toggle } = useToggle(variant === "A");
  const { weather, loading, error } = useWeatherQuery(city); // קריאת נתוני מזג האוויר

  useEffect(() => {
    // בדיקה ועדכון של הווריאנט לפי מה שנמצא ב-SessionStorage
    const storedVariant = sessionStorage.getItem("variant");
    if (storedVariant === "A" || storedVariant === "B") {
      toggle(storedVariant === "A");
    }
  }, []);

  if (loading) return <p>Loading weather data...</p>;
  if (error) return <p className="text-red-500">Error: {error}</p>;
  if (!weather) return null;

  return (
    <div className="p-6 bg-gray-800 text-white rounded-xl shadow-lg">
      <h2 className="text-3xl font-bold">{city}</h2>
      <p>{weather.weather[0].description}</p>
      <p className="text-5xl font-bold">{Math.round(weather.main.temp)}°C</p>

      {/* תצוגת מידע לפי וריאנט A או B */}
      {isVariantA ? (
        // וריאנט A
        <div className="flex justify-between mt-4">
          <div className="p-4 bg-gray-700 rounded-lg">
            <p>Humidity</p>
            <p className="text-xl font-bold">{weather.main.humidity}%</p>
          </div>
          <div className="p-4 bg-gray-700 rounded-lg">
            <p>Wind</p>
            <p className="text-xl font-bold">{weather.wind.speed} m/s</p>
          </div>
          <div className="p-4 bg-gray-700 rounded-lg">
            <p>Feels like</p>
            <p className="text-xl font-bold">{Math.round(weather.main.feels_like)}°</p>
          </div>
        </div>
      ) : (
        // וריאנט B
        <div className="flex justify-between mt-4">
          <div className="p-4 bg-gray-700 rounded-lg">
            
            <p>Humidity</p>
            <p className="text-xl font-bold">{weather.main.humidity}%</p>
          </div>
          <div className="p-4 bg-gray-700 rounded-lg">
            <p>Wind Speed</p>
            <p className="text-xl font-bold">{weather.wind.speed} m/s</p>
          </div>
        </div>
      )}
    </div>
  );
}
